$c-light-gray: #A8A8A8;
$c-gray: #6e6f6f;
$c-white: #ffffff;
$c-black: #333;

// Palette
$c-red: #ED1C24;
$c-red-dark: #C40000;

$c-yellow-lighten: #FEB802;
$c-yellow: #FFCA00;
$c-yellow-dark: #FAB701;

$c-black-lighten: #D2D2D2;
$c-black: #333;
$c-black-dark: #000;

$palette-colors: (
  'red': $c-red,
  'red-dark': $c-red-dark,

  'yellow-lighten': $c-yellow-lighten,
  'yellow': $c-yellow,
  'yellow-dark': $c-yellow-dark,

  'black-lighten': $c-black-lighten,
  'black': $c-black,
  'black-dark': $c-black-dark,
);

